html {
  overflow: hidden;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.move-card {
  position: absolute;
  border-radius: 5px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  will-change: transform;
  width: 150px;
  height: 150px;
}

.move-card:hover {
  cursor: pointer;
}

.center-box-anim {
  position: absolute;
  border-radius: 5px;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  will-change: transform;
  width: 40vw;
  height: 80vh;
}

.center-box-hidden {
  display: none;
}

.cleanLink {
  text-decoration: none;
  color: black
}

.cleanLink:hover {
  color: orange
}

.headerBox {
  display: flex;
  background:rgb(255,255,255, .73);
  border-radius: 10px;
  color: black;
  padding: 15px;
  justify-content: center;
  align-content: center;
  margin-bottom: 30px;
  max-height: 10%;
  max-width: 100%;
}

.midBox {
  display: flex;
  background: rgb(255,255,255, .73);
  border-radius: 10px;
  color: black;
  padding: 15px;
  margin: 2%;
  justify-content: center;
  align-content: center;
  width: 40%;

}

.midBoxLeft {
  display: flex;
  background: rgb(255,255,255, .73);
  border-radius: 10px;
  color: black;
  padding: 15px;
  margin: 2%;
  justify-content: center;
  align-content: center;
  width: 60%;
}

.midBoxRight {
  display: flex;
  background: rgb(255,255,255, .73);
  border-radius: 10px;
  color: black;
  padding: 15px;
  margin: 2%;
  justify-content: center;
  align-content: center;
  width: 60%;
  align-self: flex-end;
}

.midBoxRegular {
  display: flex;
  background: rgb(255,255,255, .73);
  border-radius: 10px;
  color: black;
  padding: 15px;
  margin: 2%;
  margin-left: 50px;
  justify-content: start;
  align-content: center;
  width: 80%;
}

.startUpBoxes > div {
  cursor: pointer;
  position: absolute;
  width: 93.7%;
  height: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  will-change: transform, opacity;
  text-shadow: 0px 2px 40px #00000020, 0px 2px 5px #00000030;
}

.startupBoxHeader {
  display: flex;
  background: rgb(255,255,255, .73);
  border-radius: 10px;
  color: black;
  padding: 15px;
  justify-content: start;
  align-content: center;
  margin-top: 30px;
  width: 85%;
}

.startupBoxInfo {
  display: flex;
  background: rgb(255,255,255, .73);
  border-radius: 10px;
  height: 40%;
  color: black;
  padding: 15px;
  justify-content: start;
  align-content: center;
  margin-top: 30px;
  width: 85%;
}

.midBoxNamePlate {
  display: flex;
  background: rgb(255,255,255, .73);
  border-radius: 100px;
  width: 80%;
  color: black;
  padding: 15px;
  justify-content: start;
  align-content: center;
  margin-top: 30px;
  width: 85%;
}

#header {
  height: 10vh;
  width: 100vw;
  background-color: rgba(0,0,0, 0.2);
  position: fixed;
}